import {Component, Input, OnInit} from '@angular/core';
import {map, takeWhile, timer} from 'rxjs';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-vault-signin-code',
  templateUrl: './vault-signin-code.component.html',
  styleUrls: ['./vault-signin-code.component.scss']
})
export class VaultSigninCodeComponent implements OnInit {
  loading: boolean = false
  errorMsg: string = ""

  @Input() currentEmail = "test@example.com"

  codeExpirySeconds = 300;
  timeRemaining$ = timer(0, 1000).pipe(
    map(n => (this.codeExpirySeconds - n) * 1000),
    takeWhile(n => n >= 0),
  );


  constructor(
    private routerService: Router,
    private authService: AuthService,
  ) {
    this.currentEmail = this.routerService.getCurrentNavigation()?.extras.state?.["currentEmail"]
  }

  ngOnInit(): void {
  }

  onCodeCompleted(code: string){
    this.loading = true

    console.log("submit finish", this.currentEmail, code)
    this.authService.VaultAuthFinish(this.currentEmail, code)
      .then(() => {
        this.loading = false
        //this will attempt to redirect to Dashboard, then fallback to ID verification if not provided
        this.routerService.navigateByUrl("dashboard")
      })
      .catch((err)=>{
        console.error(err)
        this.loading = false
        if(err?.name){
          this.errorMsg = "code is incorrect"
        } else{
          this.errorMsg = "an unknown error occurred during sign-in"
        }
        // const toastNotification = new ToastNotification()
        // toastNotification.type = ToastType.Error
        // toastNotification.message = this.errorMsg
        // this.toastService.show(toastNotification)
      })
  }

}
