import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {VaultSigninComponent} from './pages/vault-signin/vault-signin.component';
import {IsAuthenticatedAuthGuard} from './auth-guards/is-authenticated-auth-guard';
import {VaultSigninCodeComponent} from './pages/vault-signin-code/vault-signin-code.component';
import {DashboardComponent} from './pages/dashboard/dashboard.component';
import {AppComponent} from './app.component';
import {ConnectedAccountsTabComponent} from './components/connected-accounts-tab/connected-accounts-tab.component';
import {ConnectedAppsTabComponent} from './components/connected-apps-tab/connected-apps-tab.component';
import {SettingsTabComponent} from './components/settings-tab/settings-tab.component';
import {DashboardRoutingModule} from './dashboard-routing.module';

const routes: Routes = [
  // @Input routing
  // https://www.freecodecamp.org/news/use-input-for-angular-route-parameters/
  // https://angular.love/router-data-as-components-inputs-in-angular-v16


  { path: 'auth/signin', component: VaultSigninComponent },
  { path: 'auth/signin/code', component: VaultSigninCodeComponent },
  { path: '', redirectTo: '/auth/signin', pathMatch: 'full' },

  //must be at bottom of list
  { path: '**', redirectTo: 'auth/signin' },

];


@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    DashboardRoutingModule // this is the magic. I'm assuming to put it second is better (though putting it first didn't seem to have any immediate negative effect)
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
