import {Inject, Injectable} from '@angular/core';
import {environment} from '../../environments/environment';
import {HttpClient} from '@angular/common/http';
import {ResponseWrapper} from '../models/response-wrapper';
import {BehaviorSubject} from 'rxjs';
import * as jose from 'jose'
import {ConfigService} from './config.service';
import {ORG_CREDENTIAL_PUBLIC_ID} from '../app.constants';

export const FASTEN_AUTH_VAULT_COOKIE_NAME = 'fasten_connect_auth_vault'

@Injectable({
  providedIn: 'root'
})
export class AuthService {


  public IsAuthenticatedSubject = new BehaviorSubject<boolean>(false)

  constructor(private _httpClient: HttpClient, private vaultConfigService: ConfigService) {}




  public async VaultAuthBegin(email: string): Promise<any> {
    let resp = await this._httpClient.post<ResponseWrapper>(`${environment.connect_api_endpoint_base}/bridge/vault_auth_begin`, {
      "email": email,
    }, { withCredentials: true, params: {"public_id": ORG_CREDENTIAL_PUBLIC_ID} }).toPromise()
    return resp
  }

  public async VaultAuthFinish(email: string, code: string): Promise<any> {
    let resp = await this._httpClient.post<ResponseWrapper>(`${environment.connect_api_endpoint_base}/bridge/vault_auth_finish`, {
      "email": email,
      "code": code
    }, { withCredentials: true, params: {"public_id": ORG_CREDENTIAL_PUBLIC_ID} }).toPromise()
    return resp
  }

  public async Signout(): Promise<any> {
    this.publishAuthenticationState(false)
    return this.deleteCookie(FASTEN_AUTH_VAULT_COOKIE_NAME)
    // // let remotePouchDb = new PouchDB(this.getRemoteUserDb(localStorage.getItem("current_user")), {skip_setup: true});
    // if(this.pouchDb){
    //   await this.pouchDb.logOut()
    // }
    // await this.Close()
  }


  public async GetJWTPayload(): Promise<any> {
    let authToken = this.getCookie(FASTEN_AUTH_VAULT_COOKIE_NAME)
    if (!authToken) {
      return null
    }
    let jwks = jose.createRemoteJWKSet(new URL(environment.jwks_uri))
    let issuerHost = environment.connect_api_jwt_issuer_host

    try {
      //audience and issuer must be the same. This token is only valid on the fasten connect api
      const {payload, protectedHeader} = await jose.jwtVerify(authToken, jwks, {
        issuer: issuerHost,
        audience: issuerHost,
      })
      // @ts-ignore
      this.vaultConfigService.systemConfig = {user: payload}
      return payload
    } catch (e) {
      console.error("failed to verify jwt:", e, issuerHost)
      return null
    }
  }

  public async IsAuthenticated(): Promise<boolean> {
    let payload = await this.GetJWTPayload()
    let isAuthenticated = payload != null
    this.publishAuthenticationState(isAuthenticated)
    return isAuthenticated
  }

  //https://stackoverflow.com/questions/34298133/angular-cookies
  private getCookie(name: string): string {
    const ca: Array<string> = decodeURIComponent(document.cookie).split(';');
    const caLen: number = ca.length;
    const cookieName = `${name}=`;
    let c: string;

    for (let i  = 0; i < caLen; i += 1) {
      c = ca[i].replace(/^\s+/g, '');
      if (c.indexOf(cookieName) === 0) {
        return c.substring(cookieName.length, c.length);
      }
    }
    return '';
  }

  private deleteCookie(name: string) {
    this.setCookie(name, '', -99999); // - 1 was not far back enough to actually remove the cookie, this sets the time before the epoch and will cause the cookie to be deleted.
  }
  private setCookie(name: string, value: string, expireDays: number, path: string = '') {
    const d: Date = new Date();
    d.setTime(d.getTime() + expireDays * 24 * 60 * 60 * 1000);
    const expires = `expires=${d.toUTCString()}`;
    const cpath = path ? `; path=${path}` : '';
    document.cookie = `${name}=${value}; ${expires}${cpath}; SameSite=Lax`;
  }

  private publishAuthenticationState(authenticated: boolean){
    if(this.IsAuthenticatedSubject.value != authenticated){
      this.IsAuthenticatedSubject.next(authenticated)
    }
  }
}
