import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {ConfigService} from '../../services/config.service';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';
import {VaultProfile} from '../../models/fasten/vault-profile';

@Component({
  selector: 'app-vault-signin',
  templateUrl: './vault-signin.component.html',
  styleUrls: ['./vault-signin.component.scss']
})
export class VaultSigninComponent implements OnInit {
  loading: boolean = false
  showMessage: boolean = false

  submitted: boolean = false
  existingVaultProfile: VaultProfile = new VaultProfile()
  errorMsg: string = ""
  message: string = ""

  constructor(
    public configService: ConfigService,
    private authService: AuthService,
    private routerService: Router,
  ) { }

  ngOnInit(): void {
    const urlParams = new URLSearchParams(window.location.search);
    //TODO: parse urlParameters to determine the API mode
  }

  signinSubmit(){
    this.submitted = true;
    this.loading = true

    console.log("Signin", this.existingVaultProfile.email)
    this.authService.VaultAuthBegin(this.existingVaultProfile.email!)
      .then(() => {
        this.loading = false
        this.routerService.navigateByUrl( 'auth/signin/code', {
          state: {
            currentEmail: this.existingVaultProfile.email
          }
        })
      })
      .catch((err)=>{
        this.loading = false
        if(err?.name){
          this.errorMsg = "email or password is incorrect"
        } else{
          this.errorMsg = "an unknown error occurred during sign-in"
        }
        // const toastNotification = new ToastNotification()
        // toastNotification.type = ToastType.Error
        // toastNotification.message = this.errorMsg
        // this.toastService.show(toastNotification)
      })
  }

}
