import { Injectable } from '@angular/core';
import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router} from '@angular/router';
import {AuthService} from '../services/auth.service';

@Injectable()
export class IsAuthenticatedAuthGuard implements CanActivate {
  constructor(private authService: AuthService, private router: Router) {

  }

  async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise <boolean> {
    let jwtPayload = await this.authService.GetJWTPayload()

    //check if the user is authenticated, if not, redirect to login
    if (!jwtPayload) {
      return await this.router.navigate(['/auth/signin']);
    } else if(!jwtPayload.has_verified_identity){
      console.log("Profile does not have a verified identity, redirecting to id verification step", jwtPayload)
      return await this.router.navigate(['/auth/identity-verification']);
    }


    // continue as normal
    return true
  }
}
