import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {merge} from 'lodash';

class VaultSystemConfig {
}

@Injectable({
  providedIn: 'root'
})
export class ConfigService {


  //public but should only be used in HTML directly. (via `| async`)
  public systemConfigSubject: BehaviorSubject<VaultSystemConfig>;


  private _defaultSystemConfig: VaultSystemConfig;


  constructor() {
    this._defaultSystemConfig = new VaultSystemConfig()
    this.systemConfigSubject = new BehaviorSubject(this._defaultSystemConfig);

  }


  // -----------------------------------------------------------------------------------------------------
  // @ Accessors
  // -----------------------------------------------------------------------------------------------------

  /**
   * Setter & getter for config
   */

  //Setter
  set systemConfig(value: Partial<VaultSystemConfig>) {
    // get the current config, merge the new values, and then submit.
    const mergedSettings = merge({}, this.systemConfigSubject.getValue(), value);

    // Store the value
    if(JSON.stringify(mergedSettings) !== JSON.stringify(this.systemConfigSubject.getValue())){
      console.log('updating system settings:', mergedSettings)
      this.systemConfigSubject.next(mergedSettings);
    }
  }

  //Getter
  get systemConfig$(): VaultSystemConfig {
    console.log('getting cached system settings:', this.systemConfigSubject.getValue())
    return this.systemConfigSubject.getValue()
  }
}
